<template lang="pug">
.sales-board
  .sales-board-intro
    h2 流量分析
    p 是指在获得网站访问量基本数据的情况下对有关数据进行统计、分析，从中发现用户访问网站的规律，并将这些规律与网络营销策略等相结合，从而发现目前网络营销活动中可能存在的问题，并为进一步修正或重新制定网络营销策略提供依据。当然这样的定义是站在网络营销管理的角度来考虑的
  .sales-board-form
    .sales-board-line
      .sales-board-line-left 购买数量：
      .sales-board-line-right
        v-counter(
          @on-change="onParamChange('buyNum', $event)"
        )
    .sales-board-line
      .sales-board-line-left 产品类型：
      .sales-board-line-right
        v-selection(
          :selections="buyTypes",
          @on-change="onParamChange('buyType', $event)"
        )
    .sales-board-line
      .sales-board-line-left 有效时间：
      .sales-board-line-right
        v-chooser(
          :selections="periodList",
          @on-change="onParamChange('period', $event)"
        )
    .sales-board-line
      .sales-board-line-left 产品版本：
      .sales-board-line-right
        v-mul-chooser(
          :selections="versionList",
          @on-change="onParamChange('version', $event)"
        )
    .sales-board-line
      .sales-board-line-left 总价
      .sales-board-line-right {{ price }}元
    .sales-board-line
      .sales-board-line-left &nbsp;
      .sales-board-line-right
        .button(
          @click="showPayDialog"
        ) 立即购买
  .sales-board-des
    h2 产品说明
    p 网站访问统计分析报告的基础数据源于网站流量统计信息，但其价值远高于原始数据资料。专业的网站访问统计分析报告对网络营销的价值，正如专业的财务分析报告对企业经营策略的价值。
    h3 用户行为指标
    ul
      li 用户行为指标主要反映用户是如何来到网站的、在网站上停留了多长时间、访问了哪些页面等，主要的统计指标包括：
      li 用户在网站的停留时间；
      li 用户来源网站（也叫“引导网站”）；
      li 用户所使用的搜索引擎及其关键词；
      li 在不同时段的用户访问量情况等。
    h3 浏览网站方式
    ul
      li 用户上网设备类型
      li 用户浏览器的名称和版本
      li 访问者电脑分辨率显示模式
      li 用户所使用的操作系统名称和版本
      li 用户所在地理区域分布状况等
  my-dialog(
    :is-show="isShowPayDialog",
    @on-close="hidePayDialog"
  )
    table.buy-dialog-table
      tr
        th 购买数量
        th 产品类型
        th 有效时间
        th 产品版本
        th 总价
      tr
        td {{ buyNum }}
        td {{ buyType.label }}
        td {{ period.label }}
        td
          span(
            v-for="item in versions"
          ) {{ item.label }}
        td {{ price }}
    h3.buy-dialog-title 请选择银行
    bank-chooser(
      @on-change="onChangeBanks"
    )
    .button.buy-dialog-btn(
      @click="confirmBuy"
    ) 确认购买
  my-dialog(
    :is-show="isShowErrDialog",
    @on-close="hideErrDialog"
  ) 支付失败
  check-order(
    :is-show-check-dialog="isShowCheckOrder",
    :order-id="orderId",
    @on-close-check-dialog="hideCheckOrder"
  )
</template>
<script>
import _ from 'lodash'
import axios from 'axios'
import VSelection from '@/components/base/select'
import VCounter from '@/components/base/counter'
import VChooser from '@/components/base/chooser'
import VMulChooser from '@/components/base/mulChooser'
import MyDialog from '@/components/base/dialog'
import BankChooser from '@/components/base/bankChooser'
import CheckOrder from '@/components/base/checkOrder'
export default {
  data () {
    return {
      buyNum: 0,
      buyType: {},
      versions: [],
      period: {},
      price: 0,
      versionList: [
        {
          label: '客户版',
          value: 0
        },
        {
          label: '代理商版',
          value: 1
        },
        {
          label: '专家版',
          value: 2
        }
      ],
      periodList: [
        {
          label: '半年',
          value: 0
        },
        {
          label: '一年',
          value: 1
        },
        {
          label: '三年',
          value: 2
        }
      ],
      buyTypes: [
        {
          label: '入门版',
          value: 0
        },
        {
          label: '中级版',
          value: 1
        },
        {
          label: '高级版',
          value: 2
        }
      ],
      isShowPayDialog: false,
      bankId: null,
      orderId: null,
      isShowCheckOrder: false,
      isShowErrDialog: false
    }
  },
  components: {
    'v-selection': VSelection,
    'v-counter': VCounter,
    'v-chooser': VChooser,
    'v-mul-chooser': VMulChooser,
    'my-dialog': MyDialog,
    'bank-chooser': BankChooser,
    'check-order': CheckOrder
  },
  mounted () {
    this.buyNum = 1
    this.buyType = this.buyTypes[0]
    this.versions = [this.versionList[0]]
    this.period = this.periodList[0]
    this.getPrice()
  },
  methods: {
    onParamChange (attr, val) {
      this[attr] = val
      this.getPrice()
    },
    getPrice () {
      let buyVersionsArray = _.map(this.versions, (item) => {
        return item.value
      })
      let reqParams = {
        buyNumber: this.buyNum,
        buyType: this.buyType.value,
        period: this.period.value,
        version: buyVersionsArray.join(',')
      }
      axios.post('/api/getPrice', reqParams)
        .then((res) => {
          this.price = res.data.number
        })
    },
    showPayDialog () {
      this.isShowPayDialog = true
    },
    hidePayDialog () {
      this.isShowPayDialog = false
    },
    hideErrDialog () {
      this.isShowErrDialog = false
    },
    hideCheckOrder () {
      this.isShowCheckOrder = false
    },
    onChangeBanks (bankObj) {
      this.bankId = bankObj.id
    },
    confirmBuy () {
      let buyVersionsArray = _.map(this.versions, (item) => {
        return item.value
      })
      let reqParams = {
        buyNumber: this.buyNum,
        buyType: this.buyType.value,
        period: this.period.value,
        version: buyVersionsArray.join(','),
        bankId: this.bankId
      }
      axios.post('/api/createOrder', reqParams)
        .then((res) => {
          this.orderId = res.data.orderId
          this.isShowCheckOrder = true
          this.isShowPayDialog = false
        })
        .catch((err) => {
          console.log(err)
          this.isShowBuyDialog = false
          this.isShowErrDialog = true
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
  td,
  th{
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 5px 0;
  }
  th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
  }
}
</style>



