<template lang="pug">
.sales-board
  .sales-board-intro
    h2 广告发布
    p 广告活动按照广告计划执行，到完成广告创作并形成广告作品之后，经过广告主的最后审核同意，即可送到预定的媒介发布刊播。这项工作一般由媒介部门的有关专业人员负责，他们的任务就是专门负责与有关媒介单位接洽，安排有关广告的发播事宜，并对发播质量实施监督。
  .sales-board-form
    .sales-board-line
      .sales-board-line-left 购买数量：
      .sales-board-line-right
        v-counter(
          :max="100",
          :min="20"
        )
    .sales-board-line
      .sales-board-line-left 行业：
      .sales-board-line-right
        v-selection(
          :selections="tradeList"
        )
    .sales-board-line
      .sales-board-line-left 产品版本：
      .sales-board-line-right
        v-mul-chooser(
          :selections="tradeList"
        )
    .sales-board-line
      .sales-board-line-left 有效时间：
      .sales-board-line-right 半年
    .sales-board-line
      .sales-board-line-left 总价：
      .sales-board-line-right 500元
    .sales-board-line
      .sales-board-line-left &nbsp;
      .sales-board-line-right
        .button 立即购买
  .sales-board-des
    h2 产品说明
    p 广告活动按照广告计划执行，到完成广告创作并形成广告作品之后，经过广告主的最后审核同意，即可送到预定的媒介发布刊播。这项工作一般由媒介部门的有关专业人员负责，他们的任务就是专门负责与有关媒介单位接洽，安排有关广告的发播事宜，并对发播质量实施监督。
    table.sales-board-table
      tbody
        tr.ui-table-row
          td.col-first
            .intro-pic
              label 安全安保
          td
            .intro-pic
              label 办公文教
          td
            .intro-pic
              label 彩票
          td
            .intro-pic
              label 车辆物流
          td
            .intro-pic
              label 成人用品
          td
            .intro-pic
              label 出版传媒
          td
            .intro-pic
              label 电脑硬件
        tr.ui-table-row
          td.col-first
            .intro-pic
              label 电子电工
          td.col-first
            .intro-pic
              label 房地产建筑装修
          td.col-first
            .intro-pic
              label 分类平台
          td.col-first
            .intro-pic
              label 服装鞋帽
          td.col-first
            .intro-pic
              label 箱包饰品
          td.col-first
            .intro-pic
              label 化工原料制品
          td.col-first
            .intro-pic
              label 机械设备

</template>
<script>
import VSelection from '@/components/base/select'
import VCounter from '@/components/base/counter'
import VMulChooser from '@/components/base/mulChooser'
export default {
  components: {
    'v-counter': VCounter,
    'v-selection': VSelection,
    'v-mul-chooser': VMulChooser
  },
  data () {
    return {
      tradeList: [
        {
          label: '出版业',
          value: 0
        },
        {
          label: '媒体',
          value: 1
        },
        {
          label: '金融',
          value: 2
        },
        {
          label: '互联网',
          value: 3
        },
        {
          label: '游戏',
          value: 4
        }
      ],
      versionList: [
        {
          label: '初级版',
          value: 0
        },
        {
          label: '中级版',
          value: 1
        },
        {
          label: '高级版',
          value: 2
        },
        {
          label: '专家版',
          value: 3
        }
      ]
    }
  }
}
</script>



