<template lang="pug">
.sales-board
  .sales-board-intro
    h2 数据统计
    p 历史资料、科学实验、检验、统计等所获得的和用于科学研究、技术设计、查证、决策等的数值加以统计为解决方案做前期准备。
  .sales-board-form
    .sales-board-line
      .sales-board-line-left 产品类型：
      .sales-board-line-right
        v-chooser(
          :selections="buyTypes"
        )
    .sales-board-line
      .sales-board-line-left 适用地区：
      .sales-board-line-right
        v-selection(
          :selections="districts"
        )
    .sales-board-line
      .sales-board-line-left 有效时间：
      .sales-board-line-right 半年
    .sales-board-line
      .sales-board-line-left 总价：
      .sales-board-line-right 500元
    .sales-board-line
      .sales-board-line-left &nbsp;
      .sales-board-line-right
        .button 立即购买
  .sales-board-des
    h2 产品说明
    p 历史资料、科学实验、检验、统计等所获得的和用于科学研究、技术设计、查证、决策等的数值加以统计为解决方案做前期准备。
    table.sales-board-table
      tbody
        tr.ui-table-row
          td.col-first
            .intro-pic
              label 安全安保
          td
            .intro-pic
              label 办公文教
          td
            .intro-pic
              label 彩票
          td
            .intro-pic
              label 车辆物流
          td
            .intro-pic
              label 成人用品
          td
            .intro-pic
              label 出版传媒
          td
            .intro-pic
              label 电脑硬件
        tr.ui-table-row
          td.col-first
            .intro-pic
              label 电子电工
          td.col-first
            .intro-pic
              label 房地产建筑装修
          td.col-first
            .intro-pic
              label 分类平台
          td.col-first
            .intro-pic
              label 服装鞋帽
          td.col-first
            .intro-pic
              label 箱包饰品
          td.col-first
            .intro-pic
              label 化工原料制品
          td.col-first
            .intro-pic
              label 机械设备

</template>
<script>
import VSelection from '@/components/base/select'
import VChooser from '@/components/base/chooser'
export default {
  components: {
    'v-chooser': VChooser,
    'v-selection': VSelection
  },
  data () {
    return {
      buyTypes: [
        {
          label: '红色版',
          value: 0
        },
        {
          label: '绿色版',
          value: 1
        },
        {
          label: '紫色版',
          value: 2
        }
      ],
      districts: [
        {
          label: '北京',
          value: 0
        },
        {
          label: '上海',
          value: 1
        },
        {
          label: '广州',
          value: 2
        },
        {
          label: '天津',
          value: 3
        },
        {
          label: '武汉',
          value: 4
        },
        {
          label: '重庆',
          value: 5
        }
      ]
    }
  }
}
</script>



